import React, { Component } from "react";
import FormKelolaUangBank from "./Component/FormKelolaUangBank";
import FormMutasiCashDanBank from "./Component/FormMutasiCashDanBank";

class MutasiCashDanBank extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      thisDay: formatedDate,
    };
  }

  handleSubmit(data) {
    const isi = {
      jumlah: data.jumlah,
      asal: data.asal,
      tujuan: data.tujuan,
      tanggal: data.tanggal,
      keterangan: data.keterangan,
    };
    console.log(isi);
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <FormMutasiCashDanBank
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default MutasiCashDanBank;
